	module.exports = {"data":{"markdownRemark":{"html":"<p>I like building things that make me more productive. Things which support me in the work I do. One of them is Universal Copy.</p>\n<p>Using this, we can copy stuff/text from Chrome(Web) and paste it on your mobile(Android). The vice-versa has not been done yet. But that’s the concept.</p>\n<p>We use many devices daily. And sharing content between them is hard. For documents and files, we have things like Google Drive and Dropbox. But if you want a link/url that you want to send or a piece of text like address that you SMS your friend, then we find it hard to send it to mobile.</p>\n<p>So I, with help of my friend, <a href=\"https://instagram.com/m_sairaghava\">Raghava</a>, built this to integrate clipboards of the devices that we use in a daily basis.</p>","frontmatter":{"path":"/projects/universal-copy","title":"Universal Copy","technologies":"Android, JS, Firebase","source":"","link":""}}},"pathContext":{}}

